.paginator__wrapp{
   display: flex;
   align-items: center;
   justify-content: center;
   padding: 2rem 0;
   & span{
       margin-right: 5px;
       font-size: 1.3rem;
       cursor: pointer;
   }
   & button{
    width: 4rem;
    height: 2rem;
    margin-top: 6px;
    border: none;
    background: #02569c;
    color: #fff;
    font-weight: 600;
    cursor: pointer;
    outline: none;
    border-radius: 4px;
    transition: all .5s ease;
    font-size: 11px;
    letter-spacing: 1px;
   }
}

.selected{
    background: #02569c;
    color: #ffffff;
    width: 18px;
    height: 18px;
    text-align: center;
    border-radius: 5px;
    display: flex;
    align-items: center;
    justify-content: center;

}